'use client'

import { useState } from 'react'
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import {
  Carousel,
  CarouselContent,
  CarouselItem,
  CarouselNext,
  CarouselPrevious,
} from "@/components/ui/carousel"

interface Restaurant {
  id: string
  name: string
  location: string
  rating: number
  hygieneScore: number
  image: string
  lastInspection: string
  cuisine: string
}

const RESTAURANTS: Restaurant[] = [
  {
    id: '1',
    name: 'Paradise Biryani',
    location: 'Secunderabad',
    rating: 4.5,
    hygieneScore: 95,
    image: 'https://images.pexels.com/photos/1639562/pexels-photo-1639562.jpeg',
    lastInspection: '2024-01-15',
    cuisine: 'Hyderabadi'
  },
  {
    id: '2',
    name: 'Bawarchi',
    location: 'RTC X Roads',
    rating: 4.3,
    hygieneScore: 92,
    image: 'https://images.pexels.com/photos/958545/pexels-photo-958545.jpeg',
    lastInspection: '2024-01-10',
    cuisine: 'North Indian'
  },
  {
    id: '3',
    name: 'Chutneys',
    location: 'Jubilee Hills',
    rating: 4.7,
    hygieneScore: 98,
    image: 'https://images.pexels.com/photos/941861/pexels-photo-941861.jpeg',
    lastInspection: '2024-01-20',
    cuisine: 'South Indian'
  },
  {
    id: '4',
    name: 'Ohri\'s Banjara',
    location: 'Banjara Hills',
    rating: 4.4,
    hygieneScore: 94,
    image: 'https://images.pexels.com/photos/67468/pexels-photo-67468.jpeg',
    lastInspection: '2024-01-12',
    cuisine: 'Multi-cuisine'
  },
  {
    id: '5',
    name: 'Minerva Coffee Shop',
    location: 'Himayatnagar',
    rating: 4.2,
    hygieneScore: 90,
    image: 'https://images.pexels.com/photos/1307698/pexels-photo-1307698.jpeg',
    lastInspection: '2024-01-18',
    cuisine: 'South Indian'
  },
  {
    id: '6',
    name: 'Shah Ghouse',
    location: 'Tolichowki',
    rating: 4.6,
    hygieneScore: 93,
    image: 'https://images.pexels.com/photos/2641886/pexels-photo-2641886.jpeg',
    lastInspection: '2024-01-14',
    cuisine: 'Hyderabadi'
  }
]

export default function RestaurantExplorer() {
  const [selectedCuisine, setSelectedCuisine] = useState<string>('all')

  const filteredRestaurants = selectedCuisine === 'all'
    ? RESTAURANTS
    : RESTAURANTS.filter(r => r.cuisine === selectedCuisine)

  const cuisineTypes = ['all', ...new Set(RESTAURANTS.map(r => r.cuisine))]

  const getHygieneScoreColor = (score: number) => {
    if (score >= 95) return 'text-green-600'
    if (score >= 90) return 'text-yellow-600'
    return 'text-red-600'
  }

  const renderStars = (rating: number) => {
    return '★'.repeat(Math.floor(rating)) + '☆'.repeat(5 - Math.floor(rating))
  }

  return (
    <div className="min-h-screen bg-gray-50 p-8">
      <div className="max-w-7xl mx-auto space-y-8">
        <div className="text-center space-y-4">
          <h1 className="text-4xl font-bold">Explore Hyderabad Restaurants</h1>
          <p className="text-gray-600">
            Discover top-rated restaurants with verified hygiene standards
          </p>
        </div>

        {/* Cuisine Filter */}
        <div className="flex justify-center space-x-2">
          {cuisineTypes.map(cuisine => (
            <Button
              key={cuisine}
              variant={selectedCuisine === cuisine ? "default" : "outline"}
              onClick={() => setSelectedCuisine(cuisine)}
              className={selectedCuisine === cuisine ? "bg-black text-white" : ""}
            >
              {cuisine.charAt(0).toUpperCase() + cuisine.slice(1)}
            </Button>
          ))}
        </div>

        {/* Featured Restaurants Carousel */}
        <Card className="p-6">
          <h2 className="text-2xl font-semibold mb-6">Featured Restaurants</h2>
          <Carousel className="w-full">
            <CarouselContent>
              {filteredRestaurants.map((restaurant) => (
                <CarouselItem key={restaurant.id} className="md:basis-1/2 lg:basis-1/3">
                  <div className="p-2">
                    <Card className="overflow-hidden">
                      <img
                        src={restaurant.image}
                        alt={restaurant.name}
                        className="w-full h-48 object-cover"
                      />
                      <div className="p-4 space-y-2">
                        <h3 className="text-lg font-semibold">{restaurant.name}</h3>
                        <p className="text-gray-600">{restaurant.location}</p>
                        <div className="flex justify-between items-center">
                          <span className="text-yellow-500">{renderStars(restaurant.rating)}</span>
                          <span className={`font-semibold ${getHygieneScoreColor(restaurant.hygieneScore)}`}>
                            {restaurant.hygieneScore}% Hygiene
                          </span>
                        </div>
                        <div className="text-sm text-gray-500">
                          Last Inspected: {restaurant.lastInspection}
                        </div>
                        <Button 
                          className="w-full mt-2 bg-black hover:bg-gray-800"
                          onClick={() => window.location.href = `/customer/restaurant/${restaurant.id}`}
                        >
                          View Details
                        </Button>
                      </div>
                    </Card>
                  </div>
                </CarouselItem>
              ))}
            </CarouselContent>
            <CarouselPrevious />
            <CarouselNext />
          </Carousel>
        </Card>

        {/* Restaurant Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {filteredRestaurants.map((restaurant) => (
            <Card key={restaurant.id} className="overflow-hidden">
              <img
                src={restaurant.image}
                alt={restaurant.name}
                className="w-full h-48 object-cover"
              />
              <div className="p-4 space-y-2">
                <h3 className="text-lg font-semibold">{restaurant.name}</h3>
                <p className="text-gray-600">{restaurant.location}</p>
                <div className="flex justify-between items-center">
                  <span className="text-yellow-500">{renderStars(restaurant.rating)}</span>
                  <span className={`font-semibold ${getHygieneScoreColor(restaurant.hygieneScore)}`}>
                    {restaurant.hygieneScore}% Hygiene
                  </span>
                </div>
                <div className="text-sm text-gray-500">
                  Last Inspected: {restaurant.lastInspection}
                </div>
                <Button 
                  className="w-full mt-2 bg-black hover:bg-gray-800"
                  onClick={() => window.location.href = `/customer/restaurant/${restaurant.id}`}
                >
                  View Details
                </Button>
              </div>
            </Card>
          ))}
        </div>
      </div>
    </div>
  )
}
